body {
  background: #e4e4e4 url("../img/noise-bg.png");
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  height: auto;
}
[ng\:cloak],
[ng-cloak],
[data-ng-cloak],
[x-ng-cloak],
.ng-cloak,
.x-ng-cloak {
  display: none !important;
}
.ngdialog.ngdialog-theme-default {
  padding-bottom: 0;
  padding-top: 0;
}
.animate-show {
  opacity: 1;
}
.md-container.md-mode-query {
  height: 7px !important;
}
.md-button-auto {
  width: auto !important;
  height: auto !important;
}
md-switch {
  margin: 0;
}
.list-group-item.active {
  background: #5567bc;
  border: none;
}
.list-group-item.active:hover {
  background: #6374c2;
}
.md-button.md-icon-button {
  margin-left: 0;
  margin-right: 0;
}
.exchange-button {
  font-size: 30px;
  color: #3d4e9c;
}
.loader-helper {
  height: 7px !important;
}
.md-bar.md-bar2 {
  height: 7px !important;
}
.animate-show.ng-hide-add,
.animate-show.ng-hide-remove {
  transition: all linear 0.5s;
}
.animate-show.ng-hide {
  opacity: 0;
}
.col-centered {
  float: none;
  margin: 0 auto;
}
.main-container {
  padding-top: 4.28em;
  padding-bottom: 9px;
}
.user-avatar {
  position: relative;
  vertical-align: middle;
  border-radius: 80px;
  -webkit-border-radius: 80px;
  -moz-border-radius: 80px;
  width: 80px;
  height: 80px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.user-avatar img {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 80px;
  max-height: 80px;
  opacity: 0;
  filter: alpha(opacity=0);
}
.user-avatar.mini {
  display: inline-block;
  width: 30px;
  height: 30px;
}
.user-avatar.mini img {
  max-width: 30px;
  max-height: 30px;
}
.list-player-name {
  display: inline-block;
  margin-left: 10px;
}
.content-wrap {
  background-color: #fff;
  padding: 20px;
  border-radius: 6px;
  margin-bottom: 10px;
  display: table;
  max-width: 768px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.sign-errors {
  max-width: 330px;
  margin: 0 auto;
  text-align: center;
}
.errors {
  color: #c90000;
}
.form-sign {
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-sign .form-sign-heading,
.form-sign .checkbox {
  margin-bottom: 10px;
  text-align: center;
  font-weight: 100;
}
.form-sign .checkbox {
  font-weight: normal;
}
.form-sign .form-control {
  position: relative;
  height: auto;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
}
.form-sign .form-group {
  margin: 0;
  padding: 0;
}
.form-sign .form-control:focus {
  z-index: 2;
}
.form-sign .form-group .form-control {
  border-radius: 0;
  margin: -1px auto;
}
.form-sign .form-group:first-of-type .form-control {
  margin-bottom: -1px;
  border-radius: 6px 6px 0 0;
}
.form-sign .form-group:last-of-type .form-control {
  border-radius: 0 0 6px 6px;
}
.complain-user {
  padding: 0;
}
.complain-user {
  margin-left: 5px;
  text-align: center;
  display: inline-block;
}
.complain-user:first-of-type {
  margin-left: 0;
}
.complain-user img {
  max-height: 40px;
  max-width: 40px;
  display: block;
}
.tooltip-inner {
  white-space: nowrap;
  padding: 5px;
  max-width: 250px;
}
.complain-user-list {
  display: inline-block;
  text-align: center;
  padding: 5px;
}
.complain-user-list .avatar {
  display: block;
  height: 80px;
}
.complain-user-list .avatar img {
  max-width: 80px;
  max-height: 80px;
  display: block;
  margin: 0 auto;
}
/* XS */
@media (max-width: 767px) {
  .content-wrap {
    padding: 5px;
  }
  .normal-text {
    display: none;
  }
  .collapsed-text {
    display: inline;
  }
  .team-a {
    border-right: 1px solid rgba(212, 212, 212, 0.6);
  }
}
/* SM */
@media (min-device-width: 768px) {
  .normal-text {
    display: inline;
  }
  .collapsed-text {
    display: none;
  }
}
/* MD */
@media (min-device-width: 992px) {
  .normal-text {
    display: inline;
  }
  .collapsed-text {
    display: none;
  }
}
/* LG */
@media (min-device-width: 1200px) {
  .normal-text {
    display: inline;
  }
  .collapsed-text {
    display: none;
  }
}
