.change-sides-button {
  margin-top: 10px !important;
}
.change-sides-button:hover {
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  -moz-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.exchange {
  position: relative;
  width: 30px;
  height: 30px;
}
.exchange .left-exchange {
  position: absolute;
  top: -5px;
  left: 0;
  color: #c01935;
}
.exchange .right-exchange {
  position: absolute;
  top: 10px;
  left: 3px;
  color: #3d4e9c;
}
.create-game-container .alert {
  margin-top: 20px;
}
.create-game-container .game-graphic {
  width: 100%;
  border: 0;
}
.create-game-container .user-avatar {
  transition-duration: 300ms;
}
.create-game-container .user-avatar .add-button {
  display: none;
}
.create-game-container .user-avatar.empty .add-button {
  display: inline-block;
}
.create-game-container .team-user:hover {
  cursor: pointer;
}
.create-game-container .team-user:hover .user-avatar {
  opacity: 0.5;
}
.create-game-container .team-user:hover .user-avatar.empty {
  opacity: 1;
}
.create-game-container .team-user:hover .user-avatar.empty .add-button {
  color: rgba(255, 255, 255, 0.75);
}
.create-game-container .user-avatar.empty {
  text-align: center;
}
.create-game-container .user-avatar.empty .add-button {
  transition-duration: 400ms;
  font-size: 25px;
  line-height: 80px;
  color: rgba(255, 255, 255, 0.25);
  padding-left: 2px;
  padding-top: 2px;
}
.create-game-container .datepicker-container {
  padding-top: 10px;
}
.create-game-container .datepicker-container > input {
  font-size: 18px;
  text-align: center;
}
/* SM */
@media (max-width: 767px) {
  .create-game-container .user-avatar.empty .add-button {
    font-size: 25px;
    line-height: 45px;
    color: rgba(255, 255, 255, 0.25);
    padding-left: 2px;
    padding-top: 2px;
  }
}
/* MD */
@media (min-width: 992px) {
  .create-game-container .result-container.main .result {
    width: 100px;
  }
}
