.btn-primary {
  color: #fff;
  background-color: #3d4e9c;
  border-color: #3d4e9c;
}
.btn-primary:focus,
.btn-primary.focus {
  color: #fff;
  background-color: #2f3c77;
  border-color: #192040;
}
.btn-primary:hover {
  color: #fff;
  background-color: #2f3c77;
  border-color: #2c3870;
}
.btn-primary:active,
.btn-primary.active,
.open > .dropdown-toggle.btn-primary {
  color: #fff;
  background-color: #2f3c77;
  border-color: #2c3870;
}
.btn-primary:active:hover,
.btn-primary.active:hover,
.open > .dropdown-toggle.btn-primary:hover,
.btn-primary:active:focus,
.btn-primary.active:focus,
.open > .dropdown-toggle.btn-primary:focus,
.btn-primary:active.focus,
.btn-primary.active.focus,
.open > .dropdown-toggle.btn-primary.focus {
  color: #fff;
  background-color: #252f5e;
  border-color: #192040;
}
.btn-primary:active,
.btn-primary.active,
.open > .dropdown-toggle.btn-primary {
  background-image: none;
}
.btn-primary.disabled:hover,
.btn-primary[disabled]:hover,
fieldset[disabled] .btn-primary:hover,
.btn-primary.disabled:focus,
.btn-primary[disabled]:focus,
fieldset[disabled] .btn-primary:focus,
.btn-primary.disabled.focus,
.btn-primary[disabled].focus,
fieldset[disabled] .btn-primary.focus {
  background-color: #3d4e9c;
  border-color: #3d4e9c;
}
.btn-primary .badge {
  color: #3d4e9c;
  background-color: #fff;
}
